    period:        { key: 'period',        label: 'Âë®Êúü',      type: 'string', description: 'ÂùóÁöÑÂë®ÊúüÂ±ûÊÄßÔºåÂ¶ÇÂë®„ÄÅÊúà' },
    time:          { key: 'time',          label: 'Êó∂Èó¥',      type: 'string', description: 'ËÆ∞ÂΩïÁöÑÊó∂Èó¥ÁÇπÔºåÂ¶Ç HH:mm' },
    duration:      { key: 'duration',      label: 'Êó∂Èïø',      type: 'number', description: '‰ªªÂä°Êàñ‰∫ã‰ª∂ÁöÑÊåÅÁª≠ÂàÜÈíüÊï∞' },
