    'extra.È°πÁõÆ': { key: 'extra.È°πÁõÆ', label: 'ÊâÄÂ±ûÈ°πÁõÆ', type: 'string', description: 'ÂÖ≥ËÅîÁöÑGTDÈ°πÁõÆ' },
